import React,{useState} from 'react'
import poza1 from '../assets/despre1.jpg'
import poza2 from '../assets/despre2.jpg'
import poza3 from '../assets/despre3.jpg'
import '../styles/Despre.css'
import ArrowLeft from '../assets/arrow-left.svg'
import ArrowRight from '../assets/arrow-right.svg'


function Despre() {
  function goLeft(){
    if(poza>0)setPoza(poza-1)
    else (setPoza(2))
  }
  function goRight(){
    if(poza<2)setPoza(poza+1)
    else setPoza(0)
  } 

  const poze=[poza1,poza2,poza3];
  const [poza,setPoza]=useState(0)

  return (
    <div className='despre'>
        <div className='despre-top' style={{backgroundImage:`url(${poze[poza]})`}}>
          <img src={ArrowLeft} className='arrow' alt='arrow' style={{left:'6%',
          borderRadius:'20px 0px 0px 20px'}} onClick={goLeft}/>
          <img src={ArrowRight} className='arrow' alt='arrow' style={{right:'6%',
          borderRadius:'0px 20px 20px 0px'}} onClick={goRight}/>

        </div>
        <div className='despre-bottom'>
            <h1>DESPRE NOI</h1>
            <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Restaurantul nostru este o poveste despre arome i pasiune, despre gustul perfect i arta gtitului,
    despre creativitatea si implicarea unor oameni dedicai care vor s v mprteasc experiene desvrite.<br/>
    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Aceste file scrise cu drag dar mai ales puse n scen zi de zi de o echip de oameni talentai, creativi i
    plini de zmbete. O energie pozitiv care ne-a motivat i ne-a adus succesul n cei 19 ani de cnd suntem n
    peisajul ieean.<br/>
    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Diversitatea meniului nostru cuprinde o selecie ampl de peste 200 de preparate culinare, att cele
    tradiionale romneti, ct i preparatele din buctaria internaional: cea italian, mexican, spaniol, 
    franuzeasc ...i asiatic. <br/>
    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Pentru c timpul este ce mai important resurs pentru noi toi, te poi bucura n cel mai scurt timp de 
    mncarea savuroas chiar la tine acas, cald i atent pregatit de chefii notri, mpreun cu cele peste 1500
    de produse din noul nostru Market on-line, pentru c restaurantul nostru i livreaz GRATUIT la orice or 
    din zi i din noapte. <br/>
    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;O poveste pe care continum s o scriem mpreun pentru a rmne aa cum am promis, un nume apreciat,
    o garanie a bunului gust, recunoscut i de ncredere n Iai.<br/>
    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Incearca noile produse!</p>
        </div>
    </div>
  )
}

export default Despre